<template>
  <div class="row">
    <router-link v-if="setLinkBack" tag="button" class="btn blue" :to="linkBack || ''" @click="progressDecrease">Назад</router-link>
    <button class="btn btn-next" type="submit" @click="progressIncrease">{{nextText}}</button>
  </div>
</template>

<script>
export default {
	props: ['linkBack', 'nextText'],
	methods: {
		setLinkBack(link){
			if (link) this.linkBack = link
			// else this.linkBack = '/'
		},
		progressDecrease(){
			this.$store.dispatch('setProgress', 'decrease')
		},
		progressIncrease(){
			this.$store.dispatch('setProgress', 'increase')
		},
	}
};
</script>

